import React, { useEffect, useState } from 'react'
import {Col,Container,Row,Button,Nav,Alert} from 'react-bootstrap'
import { useParams } from 'react-router-dom';
import TabContent from '../components/TabContent';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { addItem } from '../store';
const Box = styled.div`
padding:20px;
color:gray;
`

const YellowBtn = styled.button`
// color :white;
// font-size:30px;
// width:100%;
// padding:100px;
// border :1px solid #ccc;
// background-image:url(https://images.unsplash.com/photo-1549298916-b41d501d3772?q=80&w=1112&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D);
// background-size:cover;
// background-position :center;
background:${props => props.bg};
color: ${props => props.bg =='blue' ? 'white' : 'black'};
padding :100px;
`


const Detail = (props) => {

    const {shoes} = props;
    let {id} = useParams();
    let selproduct =shoes.find(x => x.id==id)
    const [tab,setTab]=useState(0)
    const [alert, setAlert] =useState(true)
    const [show, setShow] =useState('')
    const dispatch =useDispatch();

    useEffect(()=>{
   setTimeout(()=>{setAlert(false)},2000)
   setTimeout(()=>{setShow('end')},50)
   return()=>{
    setShow('')
   }
    }, [])
  return (
   <Container className={`start ${show}`}>
    {
        alert === true ? <Alert variant='danger'>
          2초 이내 구매시 할인</Alert> : null
    }
  
  <Box>
    <YellowBtn bg='orange'>지금구매하면 10% 할인 </YellowBtn>
    <YellowBtn bg='blue'>지금구매하면 10% 할인 </YellowBtn>


  </Box>

    <Row>
        <Col md={6}>
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl} width="100%" alt="shoes" />
        </Col>
        <Col md={6}>
        <h4 className='pt-5'>{selproduct.title}</h4>
        <p>{selproduct.content}</p>
        <p>{selproduct.price}</p>
        <Button variant="success"onClick={()=>{
            dispatch(addItem({id:
            selproduct.id, item: selproduct.title, imgUrl: selproduct.imgUrl,
            price: selproduct.price, amount :1}))
        }}>Success</Button>{' '}
        </Col>

    </Row>
    <Nav variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{
                setTab(0)
            }}>탭0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{
                setTab(1)
            }}>탭1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
            <Nav.Link  eventKey="link-2"onClick={()=>{
                setTab(2)
            }} >
             탭2
            </Nav.Link>
        </Nav.Item>
    </Nav>
    {/* <div>내용0</div>
    <div>내용1</div>
    <div>내용2</div> */}
    {/* {
        (())=>{
            if(tab===0){
                return <div>내용0</div>
            }else if(tab===1){
                return <div>내용1</div>
            }else if (tab===2) {
                return <div>내용1</div>
                
            }
        })()
    } */}
   <TabContent tab={tab}/>
   </Container>

  )
}

export default Detail